import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map, shareReplay } from 'rxjs/operators';

import { CategoriesHttpService } from '@backbase/data-ang/categories-management';
import { fromCategoriesManagementResponse, CategoryItem } from '../model/categories-list';
import { categoryListLevelParam } from '../constants';

@Injectable()
export class CategoriesService {
  constructor(private readonly categoriesManagementDataHttpService: CategoriesHttpService) {}

  getCategories(): Observable<CategoryItem[]> {
    return this.categoriesManagementDataHttpService
      .getCategories(
        {
          level: categoryListLevelParam,
        },
        'response',
      )
      .pipe(shareReplay(), map(fromCategoriesManagementResponse));
  }
}
